@import url("https://fonts.googleapis.com/css?family=Roboto:300,400,500,700");
* {
  box-sizing: border-box;
  font-family: "Roboto", sans-serif; }

body,
html {
  padding: 0;
  margin: 0;
  height: 100%; }

ul {
  list-style: none;
  padding: 0;
  margin: 0; }

.content_lmr {
  width: 100%;
  display: flex; }

.branches-wrapper {
  width: 75%;
  padding: 5%; }

.branch-list {
  position: relative; }
  .branch-list:after {
    content: '';
    width: 5px;
    height: 100%;
    background-color: #caced1;
    position: absolute;
    left: 0;
    top: 0;
    margin-top: 150px; }

.position_center .item-person__dott {
  position: absolute;
  width: 20px;
  height: 20px;
  background-color: #caced1;
  left: -9px;
  top: 140px;
  border-radius: 50%; }
  .position_center .item-person__dott:after {
    content: '';
    width: 245px;
    height: 5px;
    left: 0px;
    position: absolute;
    top: 7px;
    background-color: #caced1;
    z-index: -1; }

li.item-person {
  display: flex;
  position: relative;
  justify-content: center; }

div.item-person__wrapper {
  border: 1px solid #caced1;
  border-radius: 5px;
  display: flex;
  background-color: #fbfbfd; }

.mer-mista {
  width: 50%; }

div.item-person__wrapper.mer-mista {
  display: block; }

.mer-mista .item-person__header {
  padding: 5px; }

.mer-mista .item-person__photo {
  border-radius: 100%;
  width: 210px;
  height: 210px;
  margin: 0 auto;
  border: 1px solid #caced1;
  background-image: url("../img/merlvova.jpg"); }

.item-person__name,
.item-person__position-in-lmr {
  text-align: center;
  display: block; }

.mer-mista .item-person__footer {
  padding: 10px; }

.mer-mista .item-person__name {
  font-size: 1.5em;
  color: #0a1c27; }

.mer-mista .item-person__position-in-lmr {
  font-size: 1em;
  color: #0a1c27; }

/******************************************************/
/***********************Items is here *****************/
/******************************************************/
.branch-list__item-person.posadovci {
  margin: 20px 0; }

li.item-person.posadovci {
  justify-content: flex-start;
  margin-left: 30px;
  width: 30%; }

.item-person__managment-container {
  background-color: #fbfbfd; }

li.branch-list__item-person.posadovci .item-person__wrapper {
  display: flex;
  flex-wrap: wrap;
  width: 600px; }

.item-person__manager {
  background-color: red;
  flex-basis: 300px; }

.sidebar {
  width: 25%;
  background-color: green;
  height: 100%;
  display: block;
  position: fixed;
  right: 0; }
